@import url(https://fonts.googleapis.com/css2?family=Blaka+Hollow&family=Bungee&family=Bungee+Shade&family=Creepster&family=Faster+One&family=Frijole&family=Kdam+Thmor+Pro&family=Kumar+One&family=Luckiest+Guy&family=Monofett&family=Monoton&family=Montserrat:wght@300;400;500;600&family=Moo+Lah+Lah&family=Nosifer&family=Pacifico&family=Press+Start+2P&family=Rubik+Wet+Paint&family=Sansita+Swashed:wght@300&family=Satisfy&family=Shojumaru&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    margin: 0;
    padding: 0;
}

/*App.js*/
.App {
    background-image: url("https://www.wallpapertip.com/wmimgs/16-169711_stick-a-sticky-note.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 100%;
}
.dayTime {
    display: flex;
    justify-content: space-around;
}
.container {
    /* background: linear-gradient(to right bottom, #4a3622, #312215); */
  
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.app-wrapper {
    background-color: #12343b;
    /* background: rgba( 18, 50, 57, 0.25 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 4px );
    -webkit-backdrop-filter: blur( 4px );
    border-radius: 10px; */
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    min-width: 450px;
    min-height: 650px;
    padding: 30px;
    /* box-sizing: border-box; */
    border-radius: 10px;
    box-shadow: 3px 6px 40px #0000;
    margin-bottom: 10px;
}

/*Header.js*/
.header h1 {
    color: #fff;
    text-align: center;
    margin: 30px 0;
}

/*Form.js*/
.task-input {
    outline: none;
    width: 260px;
    padding: 15px;
    margin-right: 25px;
    font-size: 20px;
    color: #ccc;
    background-color: #000000;
    border: 1px solid #c89666;
    border-radius: 10px;
}
.button-add {
    width: 70px;
    padding: 15px 15px;
    font-size: 20px;
    border-radius: 10px;
    border: 0;
    background-color: #f1af71;
    cursor: pointer;
}

/*TodosList.jsx*/
.list-item {
    display: flex;
    margin: 20px 0;
    border: 1px solid #ccc;
    border-radius: 10px;
    padding: 10px;
    max-height: 30px;
}
.button-complete,
.button-delete,
.button-edit {
    border: none;
    outline: none;
    cursor: pointer;
    background-color: transparent;
    font-size: 28px;
    border-radius: 50%;
}
.button-complete {
    color: #ff6c6c;
    margin-right: 10px;
}
.button-edit {
    color: #e2d029;
    margin-right: 10px;
}
.button-delete {
    color: lightgreen;
}

.complete {
    -webkit-text-decoration-style: solid;
            text-decoration-style: solid;
    -webkit-text-decoration-line: line-through;
            text-decoration-line: line-through;
    -webkit-text-decoration-color: #ff6c6c;
            text-decoration-color: #ff6c6c;
    opacity: 0.6;
}

.list {
    width: 220px;
    background-color: transparent; /* #12343b */
    border: none;
    color: #ccc;
    font-size: 20px;
    padding-left: 10px;
    margin-right: 15px;
}

input:focus, button:focus {
    outline: none;
}

/***Footer****/
.footer {
    background: black;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 30px;
    position: -webkit-sticky;
    position: sticky;
    bottom: 0;
}
.anchor {
    text-decoration: none;
}
small {
    color: #fff;
    margin-right: 10px;
}
.git, .in, .tube, .gram {
    font-size: 25px;
    margin-right: 10px;
}

.git {
    color: #fff;
}
.anchor1:hover::before {
    content: "GitHub";
    color: white;
    display: block;
    font-size: 10px;
    font-weight: bold;
}

.in {
    color: rgb(53, 93, 239);
}
.anchor2:hover::before {
    content: "LinkedIn";
    color: white;
    display: block;
    font-size: 10px;
    font-weight: bold;
}

.tube {
    color: rgb(239, 53, 53);
}
.anchor3:hover::before {
    content: "YouTube";
    color: white;
    display: block;
    font-size: 10px;
    font-weight: bold;
}

.gram {
    color: rgb(231, 51, 120);
}
.anchor4:hover::before {
    content: "Instagram";
    color: white;
    display: block;
    font-size: 10px;
    font-weight: bold;
}
.logoClock {
    background: black;
    display: flex;
    justify-content: space-around;
    width: 100%;
    height: 30px;
    position: fixed;
    top: 0;
}

/***logo***/
.header__title {
    color: #fff;
    font-family: 'Satisfy', cursive;
    
}
.header__title span {
    color: #4db5ff;
}
.check {
    color: rgb(229, 186, 29);
}

/***Time***/
.clock {
    color: rgb(238, 225, 225);
    font-size: 25px;
    font-weight: bold;
    font-family: 'Courier New', Courier, monospace;
}
.time {
    color:rgb(229, 186, 29);
}

/***Day***/
.day{
    color: #fff;
    font-size: 25px;
    font-weight: bold;
    position: absolute;
}
.md {
    color: rgb(229, 186, 29);
}



